<template>

    <el-card class="login-box">
      <h1 class="me-author-name">码神之路</h1>
      <div class="me-author-description">
        <span><i class="el-icon-location-outline"></i> &nbsp;江西&上饶</span>
        <span><i class="me-icon-job"></i> &nbsp;java技术专家</span>
      </div>
      <div class="me-author-tool">
        <a href="tencent://AddContact/?fromId=45&fromSubId=1&subcmd=all&uin=2496825140" :title="qq.title" class="iconfont icon-qq"/>
        <a href="https://space.bilibili.com/2496825140" :title="bilibili.title" class="iconfont icon-bilibili-fill"/>
        <div class="me-author-tool">
            <img src="static/tag/heart.png" style="width: 25px;height: 25px" @click="toAddZan()"/>
            <span style="color: red">{{this.zan}}</span>
        </div>
      </div>

    </el-card>

</template>

<script>
import {addzan ,findZan} from '@/api/add'
  export default {
    name: 'CardMe',
    data() {
      return {
        zan: 0,
        qq: {
          title: 'QQ'
        },
        bilibili: {
          title: '哔哩哔哩'
        }
        /*qq: {title: 'QQ', message: <a href="http://wpa.qq.com/msgrd?v=3&uin=2496825140&site=qq&menu=yes"/> },
        bilibili: {
          title: '哔哩哔哩',
          message: '<a target="_blank" href="https://space.bilibili.com/473844125">https://space.bilibili.com/473844125</a>'
        }*/
      }
    },
    created(){
      this.showZan()
    },
    methods: {
      showTool(tool) {
        this.$message({
          duration: 0,
          showClose: true,
          dangerouslyUseHTMLString: true,
          message: '<strong>' + tool.message + '</strong>'
        });
      },
      toAddZan() {
        /*addzan().then(data => {
            if(data.code === 200 && localStorage.getItem("zan") === "0"){
              this.$message({type: 'success', message: '点赞成功!', showClose: true})
              localStorage.setItem("zan","1")
              this.showZan()
            }else if (data.code === 200 && localStorage.getItem("zab") !== "0"){
              this.$message({type: 'error', message: '不能重复点赞哦!', showClose: true})
            }else{
              this.$message({type: 'error', message: '点赞失败！',showClose: true})
            }
        })*/
        if(localStorage.getItem("zan") === "0"){
          addzan().then(data => {
            this.$message({type: 'success', message: '点赞成功!', showClose: true})
            localStorage.setItem("zan","1")
            this.showZan()
          })
        }else if (localStorage.getItem("zab") !== "0"){
          this.$message({type: 'error', message: '不能重复点赞哦!', showClose: true})
        }else{
          this.$message({type: 'error', message: '点赞失败！',showClose: true})
        }
      },
      showZan() {
        findZan().then(data => {
          this.zan = data.data
        })
      }
    }

  }
</script>

<style scoped>
  .me-author-name {
    text-align: center;
    font-size: 30px;
    border-bottom: 1px solid #5FB878;
  }

  .me-author-description {
    padding: 8px 0;
  }

  .me-icon-job {
    padding-left: 16px;
  }

  .me-author-tool {
    text-align: center;
    padding-top: 10px;
  }

  .me-author-tool i {
    cursor: pointer;
    padding: 4px 10px;
    font-size: 30px;
  }
  .login-box{
    background-color: rgba(104, 107, 199, 0.5);
  }
</style>



